import { Box, Stack } from '@material-ui/core';
import React, { Fragment } from 'react';

import Certifications from '../components/Resume/Certifications';
import Education from '../components/Resume/Education';
import Experience from '../components/Resume/Experience';
import PageContentLayout from '../layout/PageContentLayout';
import PageLayout from '../layout/PageLayout';
import { ReactNode } from 'react';
import References from '../components/Resume/References';
import Skills from '../components/Resume/Skills';
import StyledLink from '../components/StyledLink';
import Summary from '../components/Resume/Summary';

interface Section {
  title: string;
  details: ReactNode;
}
const sections: Section[] = [
  {
    title: 'Summary',
    details: <Summary />,
  },
  {
    title: 'Education',
    details: <Education />,
  },
  {
    title: 'Experience',
    details: <Experience />,
  },
  {
    title: 'Skills',
    details: <Skills />,
  },
  {
    title: 'Certifications',
    details: <Certifications />,
  },
  {
    title: 'References',
    details: <References />,
  },
];

const ResumeNavigation = () => {
  return (
    <Stack
      direction="row"
      sx={{
        flexWrap: 'wrap',
      }}>
      {sections.map((section: Section) => (
        <StyledLink
          key={section.title}
          to={`#${section.title.toLowerCase()}`}
          label={section.title}
          bold={true}
          capitalized={true}
          typographyProps={{
            variant: 'overline',
            mr: 2,
          }}
        />
      ))}
    </Stack>
  );
};

interface IResumeProps {}

const Resume = ({}: IResumeProps) => {
  const pageName = 'Resume';
  return (
    <>
      <PageLayout title={pageName} description={pageName}>
        <PageContentLayout header={pageName} subHeader={<ResumeNavigation />}>
          <Stack spacing={2}>
            {sections.map((section: Section) => (
              <Box key={section.title} display="flex" pb={2}>
                {section.details}
              </Box>
            ))}
          </Stack>
        </PageContentLayout>
      </PageLayout>
    </>
  );
};

export default Resume;
