export interface ISkill {
    title: string;
    competency: number;
    categories: string[]
}

export interface ICategory {
    name: string;
    color: string;
}

const skills: ISkill[] = [
    {
        title: 'Javascript',
        competency: 4,
        categories: ['Web Development', 'Languages', 'Javascript'],
    },
    {
        title: 'React',
        competency: 3,
        categories: ['Web Development', 'Javascript'],
    },
    {
        title: 'Typescript',
        competency: 4,
        categories: ['Web Development', 'Languages', 'Javascript'],
    },
    {
        title: 'HTML + SASS / SCSS / CSS',
        competency: 3,
        categories: ['Web Development', 'Languages'],
    }
].map((skill) => ({ ...skill, categories: skill.categories.sort() }));

// this is a list of colors that I like. The length should be == to the
// number of categories. Re-arrange this list until you find a pattern you like.
const colors = [
    '#6968b3',
    '#37b1f5',
    '#40494e',
    '#515dd4',
    '#e47272',
    '#cc7b94',
    '#3896e2',
    '#c3423f',
    '#d75858',
    '#747fff',
    '#64cb7b',
];

const categories: ICategory[] = [
    ...new Set(skills.reduce<string[]>((acc, { categories }) => acc.concat(categories), [])),
]
    .sort()
    .map((category, index) => ({
        name: category,
        color: colors[index],
    }));

export { categories, skills };