import certifications, { ICertificate } from '../../data/resume/certification';

import Certificate from './Certifications/Certificate';
import Degree from './Education/Degree';
import React from 'react';
import ResumeDetailsLayout from './ResumeDetailsLayout';
import { Typography } from '@material-ui/core';

const Experience = () => {
  return (
    <ResumeDetailsLayout title="Certifications" id="certifications">
      {certifications.map((certificate: ICertificate) => (
        <Certificate key={certificate.name} {...certificate} />
      ))}
    </ResumeDetailsLayout>
  );
};

export default Experience;
