import { ISkill, categories, skills } from '../../data/resume/skills';
import { ImageList, ImageListItem, Stack, Typography } from '@material-ui/core';

import CategoryButton from './Skills/CategoryButton';
import React from 'react';
import ResumeDetailsLayout from './ResumeDetailsLayout';
import SkillBadge from './Skills/SkillBadge';
import useBreakpoints from '../../hooks/useBreakpoints';
import { useState } from 'react';

interface IButtons {
  All: boolean;
  [key: string]: boolean;
}

interface ISkillsState {
  buttons: IButtons;
  selectedSkill: string;
}

const Skills = () => {
  const { md, sm, lg } = useBreakpoints();
  const [state, setState] = useState<ISkillsState>({
    buttons: categories
      .map((cat) => cat.name)
      .reduce<IButtons>(
        (obj, key) => ({
          ...obj,
          [key]: false,
        }),
        { All: true },
      ),
    selectedSkill: 'All',
  });

  const getSkillBadges = () => {
    // search for true active categories
    const actCat = Object.keys(state.buttons).reduce(
      (cat, key) => (state.buttons[key] ? key : cat),
      'All',
    );

    return skills
      .sort((a, b) => {
        let ret = 0;
        if (a.competency > b.competency) ret = -1;
        else if (a.competency < b.competency) ret = 1;
        else if (a.categories[0] > b.categories[0]) ret = -1;
        else if (a.categories[0] < b.categories[0]) ret = 1;
        else if (a.title > b.title) ret = 1;
        else if (a.title < b.title) ret = -1;
        return ret;
      })
      .filter((skill) => actCat === 'All' || skill.categories.includes(actCat))
      .map((skill) => (
        <ImageListItem key={skill.title}>
          <SkillBadge categories={categories} data={skill} />
        </ImageListItem>
      ));
  };

  const getButtons = () => {
    return Object.keys(state.buttons).map((key) => (
      <CategoryButton
        label={key}
        key={key}
        active={state.buttons}
        isActive={state.selectedSkill === key}
        handleClick={handleChildClick}
      />
    ));
  };

  const handleChildClick = (label: string) => {
    setState((prevState: ISkillsState) => {
      // Toggle button that was clicked. Turn all other buttons off.
      const buttons = Object.keys(prevState.buttons).reduce<IButtons>(
        (obj, key) => ({
          ...obj,
          [key]: label === key && !prevState.buttons[key],
        }),
        { All: false },
      );
      // Turn on 'All' button if other buttons are off
      buttons.All = !Object.keys(prevState.buttons).some((key) => buttons[key]);
      return { ...prevState, ...{ buttons, selectedSkill: label } };
    });
  };

  return (
    <ResumeDetailsLayout title="Skills" id="skills">
      <Typography color="text.disabled">
        Note: I think these sections are silly, but everyone seems to have one.
        Here is a *mostly* honest overview of my skills.
      </Typography>
      <Stack
        direction="row"
        sx={{
          flexWrap: 'wrap',
        }}>
        {getButtons()}
      </Stack>
      <ImageList cols={sm ? 1 : md ? 3 : 5}>{getSkillBadges()}</ImageList>
    </ResumeDetailsLayout>
  );
};

export default Skills;
