import React from 'react';
import ResumeDetailsLayout from './ResumeDetailsLayout';
import StyledNavLink from '../StyledNavLink';

const References = () => {
  return (
    <ResumeDetailsLayout title="References" id="references">
      <StyledNavLink
        to="/contact"
        label="Available upon request."></StyledNavLink>
    </ResumeDetailsLayout>
  );
};

export default References;
